body {
  margin: 0;
  padding: 0 40px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #A3ABF1;
}


* {
  font-family: 'Roboto Condensed', sans-serif;
}

//////////  HEADER
.wrapper {
  max-width: 1080px;
  padding: 10px 0;
  margin: 50px auto;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  .logo{
    margin: 0;
    padding: 0;
  }
  .headerNavActive{
    color: #A3ABF1;
  }
  .header{
    padding: 5px 40px;
    padding-bottom: 10px;
    border-bottom: 1px solid #969696;
    margin-bottom: 90px;
    justify-content: space-between;
    
    .headerNav{
      li{
        padding: 0 10px;
        &:hover{
          color: #A3ABF1;
        }
      }
    }
  }
}
@media only screen and (max-width: 500px) {
  .header{
      justify-content: center;
      .logo{
        display: flex;
        flex:1;
        justify-content: center; 
      }
      .headerNav{
        margin-top: 5px;
        display: flex;
        flex:1;
        justify-content: center; 
      }
 }
}

//////////  BUTTONS
.githubButtonGreen{
  margin-left: 5px;
  width: 150px;
  height: 40px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background: #51C036;
  color:#FFFFFF;
  transition: transform 0.2s ease-in-out;
  &:hover{
    cursor: pointer;
    transform: translateY(-5px);
  }
}
