/////////  HOME
.home{

  .homeLogo{
    margin-left: 60px;
    display: inline-block;
  }
  @media only screen and (max-width: 550px) {
    .homeLogo{
      display: none;
    }
  }


  .hoverLetterWrapper{
    padding-top: 60px;
    display: inline-block;
  }
  .githubWrapper{
    margin-top: 20px;
  }
  .hoverLetter {
      position: relative;
      text-align: center;
  }
  .hoverLetter span {
      color: #000;
      font-family: 'Roboto Condensed', sans-serif;
      font-size: 64px;
      line-height: 1.2;
      font-weight: bold;
      transition: all 0.2s ease-in-out;
      text-shadow: 0 0 4px rgba(0,0,0, 0.2); 
      position: relative;
      text-transform: uppercase;
      top: 0px;
      left: 0px;
      cursor: default;
      z-index: 1; 
  }
  .hoverLetter span:hover {
      text-shadow: -8px 8px 0 rgba(0,0,0, 0.2); 
      color: #A3ABF1;
      top: -8px;
      left: 8px;
      z-index: 2; 
  }

  .worksWrapper{
    padding: 0 30px;
  }

  .technoWrapper{
    padding: 0 30px;
    li{
      b{
        font-size: 19px;
      }
    }
  }

  .card {
    z-index: 1;
    position: relative;
    margin-top: 20px;
    border: 1px solid #D4D4D4;
    box-sizing: border-box;
    border-radius: 20px;
    transition: box-shadow 0.15s ease-in-out, transform 0.15s ease-in-out;
  
    &:hover{
      box-shadow: 0 14px 30px rgba(0, 0, 0, 0.2);
      transform: translateY(-5px);
      .description{
        p{
          opacity: 1;
        }
      }
    }

    .title{
      position: absolute;
      left:50%;
      transform: translateX(-50%);
      z-index: 15;
      h2{
        font-size: 22px;
        color: #FFFFFF;
      }
    }
    .description{
      position: absolute;
      left:50%;
      top:50%;  
      transform: translateY(-50%) translateX(-50%);
      z-index: 15;    
      p{
        text-align: center;
        font-size: 16px;
        color: #FFFFFF;
        opacity: 0;
      }
    }

    .cardImageWrapper{
        display: flex;
        position: relative;
        border-radius: 20px;
        &:after {
          content: '\A';
          position: absolute;
          border-radius: 20px;
          width: 100%; height:100%;
          top:0; left:0;
          background:rgba(0,0,0,0.65);
          opacity: 1;
        }
        img{
          flex: 1;       
          border-radius: 20px;
        }
    }   
   
  }

}

////////  CLOTH EFFECT
.clothWrapper{
  h2{
    font-size: 18px;
    margin: 0 0;
  }
}
@media only screen and (max-width: 1200px) {
  .tearableCloth{
    display: none;
  }
}


.overlay {
  
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0,0,0,0.5);
  width: 100%;
  z-index: 10;
  height: 100%;
  .drawer{
    position: fixed;
    max-height: 100%;
    padding: 10px 25px;
    border-radius: 20px;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: #FFFFFF;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    h2{
      text-align: center;
    }
    .hideOverlay{
      position: absolute;
      right: 15px;
      top: 15px;
      img{
        &:hover{
          cursor: pointer;
          transform: translateY(-1px);
        }
      }
    }
  }
  
  .sliderWrapper{
    display: block;
    width: 80%;
  } 
  .overlayContent{
    display: inline-block;
    padding: 0 10px;
    ul{
      li{
        b{font-size: 16px;}
      }
    }
  }
  .overlayTitle{
    h1{
      margin:0 0 15px 0;
    }
  }

  .technologies{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    h2{
      text-align: center;
      margin: 0;
      margin-bottom: 5px;
    }
  }
  .description{
    display: flex;
    flex: 1;
    margin-bottom: 5px;
    h2{
      text-align: center;
      margin: 0;
      margin-bottom: 5px;
    }
    p{
      max-width: 600px;
      max-height: 280px;
      overflow: auto;
      text-align: left;
      margin: 0;
      font-size: 16px;
    }
  }
}


